@import url(https://fonts.googleapis.com/css?family=Inconsolata);
.App {
  font-family: sans-serif;
  text-align: center;
}
html,
body {
  background-color: black;
  font-family: "Inconsolata", monospace;
  font-size: 1.13em;
  font-weight: bold;
}

.container {
  display: flex;
  align-content: center;
  justify-content: center;
  height: 100vh;
}

.btn {
  color: #101832;
  cursor: pointer;
  display: inline-block;
  letter-spacing: 0.075em;
  padding: 0.8em 1em;
  margin: auto 2em;
  position: relative;
  align-self: center;
  text-transform: uppercase;
  border: 3px #00bcdd solid;
  border-image: linear-gradient(45deg, #00bcdd 0%, #ff00ff 100%);
  border-image-slice: 1 1 0 0;
  z-index: 1;
  box-shadow: -0.5em 0.5em rgba(16, 24, 50, 0);
  -webkit-transform-origin: left bottom;
          transform-origin: left bottom;
  transition: all 200ms ease-in-out;
}
.btn:before,
.btn:after {
  border: 3px #00bcdd solid;
  content: "";
  display: block;
  position: absolute;
  z-index: -1;
}
.bto:before {
  border-image: linear-gradient(45deg, #00bcdd 0%, #0097dd 100%);
  border-image-slice: 1 1 0 1;
  left: -0.59em;
  top: 0.15em;
  width: 0.31em;
  height: 100%;
  -webkit-transform: skewY(-45deg);
          transform: skewY(-45deg);
}
.btn:after {
  border-image: linear-gradient(45deg, #00bcdd 0%, #ff00ff 100%);
  border-image-slice: 1 1 1 0;
  bottom: -0.61em;
  right: 0.16em;
  width: 100%;
  height: 0.31em;
  -webkit-transform: skewX(-45deg);
          transform: skewX(-45deg);
}
.btn:hover {
  background-color: white;
  background-size: 90%;
  -webkit-transform: translate(0.5em, -0.5em);
          transform: translate(0.5em, -0.5em);
  box-shadow: -1em 1em 0.15em rgba(16, 24, 50, 0.1);
}
.btn:hover:before {
  background-image: url("data:image/svg+xml;base64,");
  background-size: 100%;
  background-image: linear-gradient(45deg, #00bcdd 0%, #0097dd 100%);
  height: calc(100% - 0.13em);
  border-image-slice: 1;
}
.btn:hover:after {
  background-image: url("data:image/svg+xml;base64,");
  background-size: 100%;
  background-image: linear-gradient(45deg, #00bcdd 0%, #ff00ff 100%);
  width: calc(100% - 0.13em);
  border-image-slice: 1;
}

.cta {
  background-color: #00bcdd;
  background-image: url("data:image/svg+xml;base64,");
  background-size: 100%;
  background-image: linear-gradient(45deg, #00bcdd 0%, #ff00ff 100%);
  border-image: linear-gradient(45deg, #44e3ff 0%, #ff66ff 100%);
  border-image-slice: 1;
  color: white;
}
.cta:before {
  border-image-slice: 1;
  background-image: url("data:image/svg+xml;base64,");
  background-size: 100%;
  background-image: linear-gradient(45deg, #00bcdd 0%, #0097dd 100%);
  left: -0.75em;
  top: 0.15em;
}
.cta:after {
  border-image-slice: 1;
  background-image: url("data:image/svg+xml;base64,");
  background-size: 100%;
  background-image: linear-gradient(45deg, #00bcdd 0%, #ff00ff 100%);
  bottom: -0.75em;
  right: 0.15em;
}
.cta:hover {
  background: white;
  border-image: linear-gradient(45deg, #00bcdd 0%, #ff00ff 100%);
  border-image-slice: 1;
  color: #101832;
}
.cta:hover:before {
  height: 100%;
}
.cta:hover:after {
  width: 100%;
}

.bg {
  background: none;
  border: 3px solid #101832;
  color: #101832;
}
.bg:before,
.bg:after {
  background: #101832;
  border: 3px solid #101832;
}
.bg:hover {
  border-image: none;
}
.bg:hover:before,
.bg:hover:after {
  background: #101832;
}

